/* ======================================
   Question Card 메인 컨테이너
====================================== */
.question-card {
  background: #ffffff;
  padding: 32px;
  border-radius: 24px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.08);
  animation: fadeIn 0.3s ease;
  max-width: 800px;
  margin: 0 auto;
}

@keyframes fadeIn {
  from { 
    opacity: 0; 
    transform: translateY(12px); 
  }
  to { 
    opacity: 1; 
    transform: translateY(0); 
  }
}

/* ======================================
   헤더 영역
====================================== */
.q-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 2px solid #f0f0f0;
}

.chapter-label {
  display: flex;
  align-items: center;
  gap: 10px;
}

.chapter-index {
  font-size: 18px;
  font-weight: 700;
  color: #667eea;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.chapter-name {
  font-size: 17px;
  font-weight: 600;
  color: #2d3748;
}

.q-progress-group {
  font-size: 15px;
  background: linear-gradient(135deg, #667eea15 0%, #764ba215 100%);
  padding: 8px 16px;
  border-radius: 12px;
  color: #667eea;
  font-weight: 700;
  border: 2px solid #667eea30;
}

/* ======================================
   질문 텍스트
====================================== */
.q-text {
  font-size: 24px;
  font-weight: 700;
  color: #2d3748;
  margin-bottom: 28px;
  line-height: 1.5;
}

/* ======================================
   옵션 버튼들
====================================== */
.q-options {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 28px;
}

.q-option {
  display: flex;
  align-items: center;
  gap: 14px;
  padding: 16px 20px;
  font-size: 17px;
  font-weight: 600;
  border-radius: 16px;
  background: #f7fafc;
  border: 2px solid #e2e8f0;
  cursor: pointer;
  transition: all 0.25s ease;
  position: relative;
  overflow: hidden;
}

.q-option::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #667eea15 0%, #764ba215 100%);
  transition: left 0.3s ease;
}

.q-option:hover {
  border-color: #667eea;
  transform: translateX(4px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.15);
}

.q-option:hover::before {
  left: 0;
}

.q-option.selected {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-color: #667eea;
  color: white;
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.35);
  transform: translateX(4px);
}

/* 번호 원형 */
.option-number {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 36px;
  height: 36px;
  background: #667eea;
  color: white;
  border-radius: 50%;
  font-weight: 700;
  font-size: 16px;
  flex-shrink: 0;
  position: relative;
  z-index: 1;
  box-shadow: 0 2px 8px rgba(102, 126, 234, 0.3);
}

.q-option.selected .option-number {
  background: white;
  color: #667eea;
}

.option-text {
  flex: 1;
  position: relative;
  z-index: 1;
}

/* ======================================
   텍스트 입력
====================================== */
.q-textarea {
  width: 100%;
  min-height: 160px;
  border-radius: 16px;
  border: 2px solid #e2e8f0;
  padding: 18px;
  font-size: 17px;
  font-family: inherit;
  resize: vertical;
  outline: none;
  transition: all 0.25s ease;
  background: #f7fafc;
}

.q-textarea:focus {
  border-color: #667eea;
  background: white;
  box-shadow: 0 0 0 4px rgba(102, 126, 234, 0.1);
}

/* ======================================
   버튼 영역 (Footer)
====================================== */
.q-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 32px;
  gap: 12px;
}

/* 공통 버튼 스타일 */
.back-btn,
.skip-btn,
.next-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 14px 28px;
  border-radius: 16px;
  font-size: 16px;
  font-weight: 700;
  border: none;
  cursor: pointer;
  transition: all 0.25s ease;
  position: relative;
  overflow: hidden;
  min-width: 120px;
}

.btn-icon {
  font-size: 18px;
  transition: transform 0.25s ease;
}

.btn-text {
  font-weight: 700;
  letter-spacing: 0.3px;
}

/* Back 버튼 - 보라색 */
.back-btn {
  background: linear-gradient(135deg, #9b59b6 0%, #8e44ad 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(155, 89, 182, 0.3);
}

.back-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(155, 89, 182, 0.4);
}

.back-btn:hover .btn-icon {
  transform: translateX(-3px);
}

.back-btn:active {
  transform: translateY(0);
}

/* Skip 버튼 - 노란색/오렌지 */
.skip-btn {
  background: linear-gradient(135deg, #f39c12 0%, #e67e22 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(243, 156, 18, 0.3);
}

.skip-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(243, 156, 18, 0.4);
}

.skip-btn:hover .btn-icon {
  transform: scale(1.2);
}

.skip-btn:active {
  transform: translateY(0);
}

/* Next 버튼 - 파란색 (비활성) */
.next-btn {
  background: #cbd5e0;
  color: #a0aec0;
  cursor: not-allowed;
  box-shadow: none;
}

/* Next 버튼 - 파란색 (활성) */
.next-btn.active {
  background: linear-gradient(135deg, #3498db 0%, #2980b9 100%);
  color: white;
  cursor: pointer;
  box-shadow: 0 4px 15px rgba(52, 152, 219, 0.3);
}

.next-btn.active:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(52, 152, 219, 0.4);
}

.next-btn.active:hover .btn-icon {
  transform: translateX(3px);
}

.next-btn.active:active {
  transform: translateY(0);
}

/* ======================================
   반응형 디자인
====================================== */
@media (max-width: 768px) {
  .question-card {
    padding: 24px;
  }

  .q-text {
    font-size: 20px;
  }

  .q-option {
    padding: 14px 16px;
  }

  .option-number {
    min-width: 32px;
    height: 32px;
    font-size: 14px;
  }

  .q-footer {
    flex-wrap: wrap;
  }

  .back-btn,
  .skip-btn,
  .next-btn {
    flex: 1;
    min-width: 100px;
  }
}

@media (max-width: 480px) {
  .question-card {
    padding: 20px;
  }

  .q-footer {
    flex-direction: column;
    gap: 10px;
  }

  .back-btn,
  .skip-btn,
  .next-btn {
    width: 100%;
  }

  .chapter-label {
    flex-direction: column;
    align-items: flex-start;
    gap: 6px;
  }

  .q-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }
}

/* ======================================
   로딩 애니메이션
====================================== */
.fade-in {
  animation: fadeIn 0.3s ease;
}
